import useDeviceSize from '../../../hooks/useDeviceSize';
import { StyledHello, StyledMobileHello } from './Hello.styled';

const Hello = () => {
  const { isDesktop, isTablet, isMobile } = useDeviceSize();
  return (
    <>
      {isDesktop && (
        <StyledHello>
          <p>,</p>
          <p>   :)</p>
        </StyledHello>
      )}
      {isMobile && (
        <StyledMobileHello>
          <p>,</p>
          <p>   :)</p>
        </StyledMobileHello>
      )}
    </>
  );
};

export default Hello;
